import React, { useEffect, useState } from "react";
import Main from "../../elements/Main/Main";
import PropTypes from "prop-types";
import { CSSTransition, TransitionGroup } from "react-transition-group";

const menus = [
  {
    name: "skill",
    children: "Skill",
  },
  {
    name: "profile",
    children: "Profile",
    className: "my-5",
  },
  {
    name: "achievement",
    children: "Achievement",
  },
];

export default function About() {
  const [open, setOpen] = useState("");

  return (
    <Main
      bgClass="bg-orange2"
      className="flex flex-col md:flex-row md:justify-evenly items-center text-orange2-light"
    >
      {/* {items.map((i,) => ( */}
      {menus.map((i) => (
        <SubMenu
          useOpen={[open, setOpen]}
          name={i.name}
          className={i.className}
        >
          {i.children}
        </SubMenu>
      ))}
    </Main>
  );
}

export function SubMenu(props) {
  const { children, name, className, useOpen } = props;
  const [width, setWidth] = useState("md:w-72  hover:-translate-y-2");
  const [height, setHeight] = useState("md:h-72 h-48  ");
  const [open, setOpen] = useOpen;

  useEffect(() => {
    if (open === name) {
      setWidth("");
      setHeight("h-96 ");
    } else if (open === "") {
      setWidth("md:w-72  hover:-translate-y-2");
      setHeight("md:h-72 h-48 ");
    } else {
      setWidth("md:w-0.5 opacity-0");
    }
  }, [open]);

  const handleOpen = () => {
    if (open === "") {
      setOpen(name);
    } else {
      setOpen("");
    }
  };

  return (
    <div
      onClick={() => handleOpen()}
      className={
        "w-full transition-all duration-300 cursor-pointer transform " + width
      }
    >
      <div
        className={
          height +
          " border-2 border-white rounded-xl relative  transition-all duration-300 " +
          className
        }
      >
        <div className="w-full border-b-2 border-white flex justify-between items-center px-2 absolute">
          <h5 className="text-white">{name}</h5>
          <div className="grid grid-cols-3 gap-1">
            <div className="md:w-3 w-2 md:h-3 h-2 rounded-full bg-orange2-light" />
            <div className="md:w-3 w-2 md:h-3 h-2 rounded-full bg-orange2-light" />
            <div className="md:w-3 w-2 md:h-3 h-2 rounded-full bg-orange2-light" />
          </div>
        </div>
        <div className="p-3 pt-8 md:pt-10 h-full ">
          <div className=" bg-orange2-light rounded-lg h-full w-full flex justify-center items-center ">
            <h2 className="text-orange2 font-hand">{children}</h2>
          </div>
        </div>
      </div>
    </div>
  );
}
SubMenu.defaultProps = {
  children: {},
  useOpen: [],
  name: "",
  className: "",
};

SubMenu.propTypes = {
  children: PropTypes.object,
  useOpen: PropTypes.array,
  name: PropTypes.string,
  className: PropTypes.string,
};
